"use client";

import { useSearchParams } from "next/navigation";
import { useState, useEffect } from "react";
import ProductCard from "@/components/ProductCard";
import { type Product } from "@/components/ProductCard"; // Adjust import if needed

export default function Products() {
    const searchParams = useSearchParams();
    const type = searchParams.get("type");
    const category = searchParams.get("category");
    const [products, setProducts] = useState<Product[]>([]);
    const [loading, setLoading] = useState(true);
    
    const [sortOrder, setSortOrder] = useState<string>("");
    const [minPrice, setMinPrice] = useState<string>("");
    const [maxPrice, setMaxPrice] = useState<string>("");
    const [selectedType, setSelectedType] = useState<string>(type || "");
    const [selectedCategory, setSelectedCategory] = useState<string>(category || "");

    useEffect(() => {
        const fetchProducts = async () => {
            try {
                const res = await fetch('/api/products');
                if (res.ok) {
                    const data = await res.json();
                    setProducts(data);
                }
            } catch (error) {
                console.error("Failed to fetch products", error);
            } finally {
                setLoading(false);
            }
        };
        fetchProducts();
    }, []);

    const uniqueTypes = Array.from(new Set(products.map(product => product.type)));
    const uniqueCategories = Array.from(new Set(products.map(product => product.category)));

    const filteredProducts = products.filter((product) => {
        const matchesType = selectedType ? product.type === selectedType : true;
        const matchesCategory = selectedCategory ? product.category === selectedCategory : true;
        const matchesMinPrice = minPrice ? product.price >= parseFloat(minPrice) : true;
        const matchesMaxPrice = maxPrice ? product.price <= parseFloat(maxPrice) : true;
        return matchesType && matchesCategory && matchesMinPrice && matchesMaxPrice;
    });

    // Apply sorting
    if (sortOrder === "price-asc") {
        filteredProducts.sort((a, b) => a.price - b.price);
    } else if (sortOrder === "price-desc") {
        filteredProducts.sort((a, b) => b.price - a.price);
    } else if (sortOrder === "name-asc") {
        filteredProducts.sort((a, b) => a.title.localeCompare(b.title));
    } else if (sortOrder === "name-desc") {
        filteredProducts.sort((a, b) => b.title.localeCompare(a.title));
    }

    const heading = () => {
        if (type && category) {
            return `${type.replace("-", " ").toUpperCase()} • ${category.toUpperCase()}`;
        }
        if (type) {
            return `${type.replace("-", " ").toUpperCase()}`;
        }
        if (category) {
            return `${category.toUpperCase()}`;
        }
        return "All Products";
    };

    if (loading) return <div className="text-center py-20">Loading...</div>;

    return (
        <div className="bg-white rounded-t-3xl mx-auto p-6">
            <div className="flex items-center justify-between mb-6">
                <h2 className="text-2xl font-bold text-gray-800">{heading()}</h2>
                <div className="flex items-center space-x-4">
                    <label htmlFor="sort" className="text-gray-700">Sort by:</label>
                    <select
                        id="sort"
                        className="border border-gray-300 rounded-md p-2"
                        value={sortOrder}
                        onChange={(e) => setSortOrder(e.target.value)}
                    >
                        <option value="">None</option>
                        <option value="price-asc">Price: Low to High</option>
                        <option value="price-desc">Price: High to Low</option>
                        <option value="name-asc">Name: A-Z</option>
                        <option value="name-desc">Name: Z-A</option>
                    </select>
                </div>
            </div>
            <div className="flex items-center space-x-4 mb-6">
                <label htmlFor="minPrice" className="text-gray-700">Min Price:</label>
                <input
                    type="number"
                    id="minPrice"
                    className="border border-gray-300 rounded-md p-2 w-24"
                    value={minPrice}
                    onChange={(e) => setMinPrice(e.target.value)}
                    placeholder="Min"
                />
                <label htmlFor="maxPrice" className="text-gray-700">Max Price:</label>
                <input
                    type="number"
                    id="maxPrice"
                    className="border border-gray-300 rounded-md p-2 w-24"
                    value={maxPrice}
                    onChange={(e) => setMaxPrice(e.target.value)}
                    placeholder="Max"
                />
            </div>

            <div className="flex items-center space-x-4 mb-6">
                <label htmlFor="typeFilter" className="text-gray-700">Type:</label>
                <select
                    id="typeFilter"
                    className="border border-gray-300 rounded-md p-2"
                    value={selectedType}
                    onChange={(e) => setSelectedType(e.target.value)}
                >
                    <option value="">All Types</option>
                    {uniqueTypes.map((t) => (
                        <option key={t} value={t}>{t}</option>
                    ))}
                </select>

                <label htmlFor="categoryFilter" className="text-gray-700">Category:</label>
                <select
                    id="categoryFilter"
                    className="border border-gray-300 rounded-md p-2"
                    value={selectedCategory}
                    onChange={(e) => setSelectedCategory(e.target.value)}
                >
                    <option value="">All Categories</option>
                    {uniqueCategories.map((c) => (
                        <option key={c} value={c}>{c}</option>
                    ))}
                </select>
            </div>

            {filteredProducts.length > 0 ? (
                <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-6">
                    {filteredProducts.map((product) => (
                        <ProductCard key={product.id} product={product} />
                    ))}
                </div>
            ) : (
                <p className="text-gray-500 text-center mt-12">No products found for this filter.</p>
            )}
        </div>
    );
}